import validate from './validate'
/**
 * @param time:时间,时间戳必须为数字
 * @param str:格式化之后的日期格式,如yyyy/MM/dd,默认yyyy/MM/dd hh:mm:ss
 * yyyy 年
 * MM   月
 * dd   日
 * tt   AM 、PM
 * HH   小时 12小时制
 * hh   小时 24小时制
 * mm   分钟
 * ss   秒
 * fff  毫秒数  3位
 * 默认格式   yyyy/MM/dd hh:mm:ss
 * @returns {'yyyy/MM/dd hh:mm:ss'}
 */
function formatDate (time, str) {
  let date = time || new Date()
  if (!validate.isDate(date)) {
    date = new Date(typeof time === 'number' ? time : time.replace(/-/g, '/'))
  }
  let fmt = str || 'yyyy/MM/dd hh:mm:ss'
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(
      RegExp.$1,
      (date.getFullYear() + '').substr(4 - RegExp.$1.length)
    )
  }
  const hour = date.getHours()
  let o = {
    'M+': date.getMonth() + 1,
    'd+': date.getDate(),
    'h+': hour,
    'H+': hour % 12 || 12,
    't+': hour < 12 ? 'AM' : 'PM',
    'm+': date.getMinutes(),
    's+': date.getSeconds(),
    fff: date.getMilliseconds()
  }
  for (let k in o) {
    if (new RegExp(`(${k})`).test(fmt)) {
      let str = o[k] + ''
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length === 1 ? str : str.padStart(k.length, '0')
      )
    }
  }
  return fmt
}

/**
 * @param date:日期,str: 格式化之后的字符串格式，如'yyyy-MM-dd hh:mm:ss'或'yyyy/MM/dd hh:mm'
 * @description 将日期转换为传入参数str的格式
 * @returns {yyyy-MM-dd hh:mm:ss等等}
 */
function formatDateBy (date, str) {
  let concatStr = str.indexOf('/') > -1 ? '/' : '-'
  // 当d是1-11位数字，返回的都是1970年某月某日，当d是12-16，也会返回对应的日期，当大于16位，就是不合法的日期
  const time = isDate(date) ? date : new Date(date)
  let y = str.indexOf('yyyy') > -1 ? time.getFullYear() : '' // 年份
  let m =
    str.indexOf('MM') > -1
      ? (time.getMonth() + 1).toString().padStart(2, '0')
      : '' // 月份
  let d =
    str.indexOf('dd') > -1
      ? time
          .getDate()
          .toString()
          .padStart(2, '0')
      : '' // 日子
  let hh =
    str.indexOf('hh') > -1
      ? ' ' +
        time
          .getHours()
          .toString()
          .padEnd(2, '0')
      : '' // 小时
  let mm =
    str.indexOf('mm') > -1
      ? ':' +
        time
          .getMinutes()
          .toString()
          .padEnd(2, '0')
      : '' // 分钟
  let ss =
    str.indexOf('ss') > -1
      ? ':' +
        time
          .getSeconds()
          .toString()
          .padEnd(2, '0')
      : '' // 秒数
  return (
    `${y}` +
    concatStr +
    `${m}` +
    concatStr +
    `${d}` +
    `${hh}` +
    `${mm}` +
    `${ss}`
  )
}

/**
 * @param num 整型数字(例如：1234567)
 * @param len 分割长度,默认为3
 * @param fill 分割填充符，默认为"，"
 * @returns {字符串'1,234,567'}
 */
function fillNumber (num, len, fill) {
  if (typeof num !== 'number') {
    return num
  }
  let nums = (num || 0).toString()
  let result = ''
  const index = len || 3
  while (nums.length > index) {
    result = (fill || ',') + nums.slice(-index) + result
    nums = nums.slice(0, nums.length - index)
  }
  if (nums) {
    result = nums + result
  }
  return result
}

/**
 *
 * @param string 需要处理的字符串(例如：'张三'， '1234567890')
 * @param frontLen 开头保留位数
 * @param endLen 结尾保留位数
 */
function hidden (str, frontLen, endLen) {
  if (str === undefined || str === null) {
    return ''
  }
  if (str.length === 2) {
    endLen = 0
  }
  var len = str.length - frontLen - endLen
  var xing = ''
  for (var i = 0; i < len; i++) {
    xing += '*'
  }
  return str.substring(0, frontLen) + xing + str.substring(str.length - endLen)
}

export default { formatDate, formatDateBy, fillNumber, hidden }