const ruleTrigger = {
  'el-input': ['blur', 'change'],
  'el-input-number': ['blur', 'change'],
  'el-select': 'change',
  'el-radio-group': 'change',
  'el-checkbox-group': 'change',
  'el-cascader': 'change',
  'el-time-picker': 'change',
  'el-date-picker': 'change',
  'el-rate': 'change',
  'el-switch': 'change'
}

export default ruleTrigger
