/**
 * 沙箱
 * 隔离自定义代码块中变量污染问题
 */
// 白名单
const whiteList = ['console', 'Array', 'Object', 'Math', 'Date']

// 沙箱全局代理对象类
class SandboxGlobalProxy {
  constructor (sharedState, scope, params, extraParams) {
    // 创建一个 iframe 对象，取出其中的原生浏览器全局对象作为沙箱的全局对象
    const iframe = document.createElement('iframe', { url: 'about:blank' })
    document.body.appendChild(iframe)
    const sandboxGlobal = Object.assign(scope, iframe.contentWindow, params, extraParams)

    return new Proxy(sandboxGlobal, {
      has: (target, prop) => {
        if (sharedState.includes(prop)) {
          return false
        }
        if (!target.hasOwnProperty(prop)) {
          throw new Error(`当前变量 - ${prop}! 不属于自身属性，拒绝访问!`)
        }
        return true
      }
    })
  }
}

// 函数执行
function compileCode (code) {
  code = `with(sandboxGlobal) {
    ${code}
  }`
  return new Function('sandboxGlobal', code)
}

// 创建沙箱
export default function createSandbox ({ code, ...params } = {}) {
  // 为了支持乾坤能获取一些数据
  const qiankunparams = {
    __POWERED_BY_QIANKUN__ : window.__POWERED_BY_QIANKUN__
  }
  console.log('qiankunparams', qiankunparams)
  const proxy = new SandboxGlobalProxy(whiteList, this, params, qiankunparams)
  return compileCode(code).call(proxy, proxy)
}
