export default {
  /**
   * 切换 DOM上的 class
   * @description 若 DOM 如果存在则删除它，如果不存在则添加它
   * @param { Element } el 要操作的DOM元素
   * @param { String } className class 名称
   */
  toggleClass (el, className) {
    if (el.classList) {
      el.classList.toggle(className)
    } else {
      const classes = el.className.split(' ')
      const existingIndex = classes.indexOf(className)

      if (existingIndex >= 0) {
        classes.splice(existingIndex, 1)
      } else {
        classes.push(className)
      }
      el.className = classes.join(' ')
    }
  },
  /**
   * 在 DOM 上添加 class
   * @param { Element } el 要操作的 DOM 元素
   * @param { String } className 要删除的 class
   */
  addClass (el, className) {
    if (el.classList) {
      el.classList.add(className)
    } else {
      el.className += ` ${className}`
    }
  },
  /**
   * 在 DOM 上删除 class
   * @param { Element } el 要操作的 DOM 元素
   * @param { String } className 要删除的 class
   */
  removeClass (el, className) {
    if (el.classList) {
      el.classList.remove(className)
    } else {
      el.className = el.className.replace(new RegExp('(^|\\b)' + className.split(' ').join('|') + '(\\b|$)', 'gi'), ' ')
    }
  },

  /**
   * 切换 DOM 上的 class
   * @deprecated 若 el 上存在名为 fromClassName 的 class，则将其替换为 toClassName，并返回 toClassName; 
   * @deprecated 若 el 上不存在名为 fromClassName 的 class，则添加它并删除名为 toClassName 的 class，并返回 fromClassName; 
   * @param { Element } el 要操作的DOM元素
   * @param { string } fromClassName 
   * @param { string } toClassName 
   * @returns { string } 
   */
  changeClass (el, fromClassName, toClassName) {
    const classes = el.className.split(' ')
    const existingIndex = classes.indexOf(fromClassName)
    if (existingIndex >= 0) {
      this.removeClass(el, fromClassName)
      this.addClass(el, toClassName)
      return toClassName
    } else {
      this.addClass(el, fromClassName)
      this.removeClass(el, toClassName)
      return fromClassName
    }
  },

  /**
   * 获得标签名为tagName,类名className的元素
   * @param { string } tagName 
   * @param { string } className 
   * @returns { Array<Element> } 满足条件的元素组成的数组
   */
  getClass (tagName, className) {
    if (document.getElementsByClassName) {
      // 支持这个函数
      return document.getElementsByClassName(className)
    } else {
      const tags = document.getElementsByTagName(tagName) // 获取标签
      const tagArr = [] // 用于返回类名为className的元素
      for (let i = 0; i < tags.length; i++) {
        if (tags[i].class === className) {
          tagArr[tagArr.length] = tags[i] // 保存满足条件的元素
        }
      }
      return tagArr
    }
  },

  /**
   * 获取 el 中标签名为 tagName 子元素
   * @param { Element } el 
   * @param { String } tagName 
   * @returns { Array<Node> }
   */
  getChildren (el, tagName) {
    const nodeList = el.childNodes
    let ary = []
    if (/MSIE(6|7|8)/.test(navigator.userAgent)) {
      for (let i = 0; i < nodeList.length; i++) {
        const curNode = nodeList[i]
        if (curNode.nodeType === 1) {
          ary[ary.length] = curNode
        }
      }
    } else {
      ary = Array.prototype.slice.call(el.children)
    }

    // 获取指定子元素
    if (typeof tagName === 'string') {
      for (let k = 0; k < ary.length; k++) {
        const curTag = ary[k]
        if (curTag.nodeName.toLowerCase() !== tagName.toLowerCase()) {
          ary.splice(k, 1)
          k--
        }
      }
    }

    return ary
  },

  /**
   * 获取元素中名为 attr 的样式
   * @param { Element } el 
   * @param { string } attr 
   * @returns { CSSStyleDeclaration }
   */
  getStyle (el, attr) {
    if (el.currentStyle) {
      return el.currentStyle[attr]
    } else {
      return window.getComputedStyle(el, null)[attr]
    }
  },

  /**
   * 给元素设置样式
   * @param { Element } el 
   * @param { string } attr 要设置的样式
   * @param { string } val 要设置样式的值
   */
  setStyle (el, attr, val) {
    if (el.style) {
      el.style[attr] = val
    }
  },

  /**
   * 给元素设置属性
   * @param { Element } el 
   * @param { string} attr 要设置的属性
   * @param { string } val 要设置属性的值
   */
  setAttr (el, attr, val) {
    el.setAttribute(attr, val)
  },

  /**
   * 事件绑定
   * @param { Element } el    要绑定事件的元素
   * @param { String } type   要绑定的事件
   * @param { Function } handler    事件的处理函数
   */
  bindEvent (el, type, handler) {
    if (el.attachEvent) {
      el.attachEvent('on' + type, handler)
    } else {
      el.addEventListener(type, handler, false)
    }
  },
  /**
   * 取消事件绑定
   * @param { Element } el    要取消事件的元素
   * @param { String } type   要取消的事件
   * @param { Function } handler  事件的处理函数
   */
  removeBindEvent (el, type, handler) {
    if (window.removeEventListener) {
      // 标准浏览器
      el.removeEventListener(type, handler, false)
    } else if (window.detachEvent) {
      // IE浏览器
      el.detachEvent('on' + type, handler)
    }
  },
  /**
 * 禁用 Vue 组件绑定的事件，并用 replaceEvent 替换 events 对应的事件
 * @param { Vue } componentInstance
 * @param {Array<{type:String,event:Function}>} events // 要禁用的事件列表
 * @param {Function} replaceEvent // 要替换的事件
 */
  disabledVueEvent (componentInstance, events = [], replaceEvent) {
    if (events.length) {
      events.map(ele => {
        componentInstance.$off(ele.type, ele.event)
        componentInstance.$on(ele.type, replaceEvent)
      })
    } else {
      componentInstance.$off()
      componentInstance.$on('click', replaceEvent)
    }
  },
  /**
 * 禁用元素绑定的原生事件，并用 replaceEvent 替换 events 对应的事件
 * @param { HTMLElement } el      // 元素
 * @param {Array<String>} events // 要禁用的事件列表
 * @param {Function} replaceEvent // 要替换的事件
 * @description 通过替换元素移除所有的事件
 */
  disabledNativeEvent (el, events, replaceEvent) {
    if (events.length) {
      events.map(ele => {
        this.removeBindEvent(el, ele.type, ele.event)
        this.bindEvent(el, ele.type, replaceEvent)
      })
    } else {
      const newchild = el.cloneNode(true)
      this.bindEvent(newchild, 'click', replaceEvent)
      el.parentNode.replaceChild(newchild, el)
    }
  },

  /**
 * 事件禁用
 * @param { Vue.VNode } VNode
 * @param {Array<{type:String,event:Function}>} events // 要禁用的事件列表
 * @param {Function} replaceEvent // 要替换的事件
 */
  disabledEvent (VNode, events = [], replaceEvent) {
    if (VNode) {
      if (VNode.componentInstance) {
        this.disabledVueEvent(VNode.componentInstance, events, replaceEvent)
      } else {
        this.disabledNativeEvent(VNode.elm, events, replaceEvent)
      }
    }
  },
  /**
 * 移除所有的事件
 * @param { Vue.VNode } VNode
 * @param {Array<{type:String,event:Function}>} events // 要移除的事件列表
 * @param {Function} replaceEvent // 要替换的事件
 */
  clearAllEvent (VNode, events = [], replaceEvent) {
    if (VNode) {
      if (VNode.componentInstance) {
        VNode.componentInstance.$off()
      }
      this.removeBindEvent(VNode.elm, 'click', replaceEvent)
      events.map(ele => {
        this.removeBindEvent(VNode.elm, ele.type, ele.event)
      })
    }
  }
}
