import Vue from 'vue'
import LowcodeEngine from './views/Index.vue'
import Parser from './parser/Index.vue' // 解析器容器处理
import render from './render/index' // 解析器渲染处理 render函数
import './style/index.styl'
import ref from 'vue-ref' // 快速获取子组件

const lowerComponents = require('@cseed/ui-lower-components') // 公共组件库

const install = function (Vue, options = {}) {
  if (!options.fetch) console.error('no fetch')
}

Vue.use(lowerComponents)
Vue.use(ref, { name: 'lower-ref' })

export {
  install,
  LowcodeEngine,
  Parser,
  render
}
export default {
  install,
  LowcodeEngine,
  Parser,
  render
}
